<template>
   <div class="Carousel">
        <el-carousel  class="el-carousel" :interval="4000">
            <el-carousel-item class="el-carousel-item" v-for="(car,index) in banner" :key="index">
                <img :src="car">
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
export default {
  name: 'Carousel',
  props:['type','num'],
  data () {
    return {
        banner:[]
    }
  },
  methods:{
      requestImg(){
          for(var i = 0;i<this.num;i++){
             this.banner.push(require("../assets/"+this.type+"/"+(i+1)+"-c.png"))
          }
      }

  },
  mounted(){
    this.requestImg();
  }
}
</script>

<style lang="less" scoped>
.Carousel{
      width: 1170px;
      margin: 0 auto;
      background: #000000;
      margin-bottom: 20px;
  }
  .el-carousel__item{
      img{
          width: 100%;
          height: 100%;
      }
  }
</style>



